// @use '../../index.scss' as index;

ul,
li {
    padding: 0;
    margin: 0;
}

.filtersContainer {
    margin: 0 auto;
    margin-top: 40px;
    max-width: 1024px;
}

.filtersList {
    display: flex;
    flex-wrap: wrap;
    column-gap: 15px;
    row-gap: 20px;
    display: flex;
    justify-content: center;
    list-style: none;
    margin: 0px 10px;
}

.filterItem {
    padding: 5px 15px;
    // background-color: index.$base-color;
    background-color: var(--base-color);
    border-radius: 20px;
    color: white;
    transition: 0.3s;

    &:hover {
        // outline: 1px solid index.$base-color;
        outline: 1px solid var(--base-color);
        // color: index.$base-color;
        color: var(--base-color);
        background-color: white;
        cursor: pointer;
        transform: scale(105%);
    }
}

.filterItemActive {
    // outline: 1px solid index.$base-color;
    outline: 1px solid var(--base-color);
    // color: index.$base-color;
    color: var(--base-color);
    background-color: white;
    cursor: pointer;
    transform: scale(105%);
	box-shadow: 0 0 10px 5px rgb(174, 174, 174);
}

li {
	transition: .3s;
	&:hover {
		transform: scale(110%);
		cursor: pointer;
		// box-shadow: 0 0 10px 5px rgb(174, 174, 174);
	}

    i {
        // color: index.$base-color;
        color: var(--base-color);
        font-size: 30px;
    }
}
